body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#container {
	box-shadow: 0px 0px 20px 7px #000000;
	height:100vh;
}

#logoproiect {
	padding:7px;
}

#videostream {
	max-width: 100%;
	width: auto;
	height: auto;
}

#up-arrow {
	height:100px;
	width:100px;
}

#left-arrow {
	height:100px;
	width:100px;
}

#down-arrow {
	height:100px;
	width:100px;
}

#right-arrow {
	height:100px;
	width:100px;
}

#space-arrow {
	height:40px;
	width:40px;
}

#stop-arrow {
	height:50px;
	width:50px;
	display:none;
	color: #ff0000;
}

#arrow-table {
	margin-left: auto;
	margin-right: auto;
	position: relative;
	
}

#macheta_senzori_box {
	width: 50%;
}

.macheta_senzori {
	position: absolute;
	top: 0;
	height: 320px;
	opacity: 0;
}

#macheta_senzori_verde_fata, #macheta_senzori_verde_stanga, 
#macheta_senzori_verde_spate, #macheta_senzori_verde_dreapta{
	opacity: 1;
}